import React from 'react';
import PropTypes from 'prop-types';
import VKConnect from "@vkontakte/vk-bridge";
import { platform, IOS } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import persik from '../img/kar1.jpg';
import persik2 from '../img/kar2.jpg';
//import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Button2 from '@material-ui/core/Button'
import './Persik.css';
const apps = 7566092;

const so = e => {
	VKConnect.send("VKWebAppCopyText", {"text": "Привет vk.com/app" + apps + " Зайди по фасту, тут дают аккаунты бесплатно! Быстрее"});
};

const gopuzdec = e => {
	VKConnect.send("VKWebAppAllowNotifications"); 

	VKConnect.send("VKWebAppShare", {"link": "https://vk.com/app" + apps});
};
const osName = platform();

const Persik = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			Нажимите на всё: Разрешить
		</PanelHeader>

		<h1 align="center">Вы должны подписаться на всех спонсоров. </h1>
			<h1 align="center">Разрешите всё, что требует у вас приложение.</h1>


		<Button size="xl" level="3" onClick={so} data-to="persik">
			
					Нажими чтобы скопировать
				</Button>
				<br></br>
				<div align="center" >Вы должны пригласить 30 человек (Это обязательно). </div>
				<div align="center" >
				После приглашение, приложение вас оповестит и выдадим вам аккаунт.
					</div>
					<div align="center" >
					Скопировать текст можете нажав кнопку выше. Чтобы быстро набрать, можете отправлять в комментарии
					</div>
					<br></br>
					<div align="center" >
					<Button2 classname= 'App' variant="outlined" color="primary" onClick={gopuzdec}>
								Быстро набрать людей
							</Button2>	
							</div>
							<img className="Persik" src={persik} alt="Persik The Cat"/>
			<img className="Persik" src={persik2} alt="Persik The Cat"/>


	</Panel>
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};



export default Persik 


